#contact{
    text-align: center;
    margin-top: 2rem;
}

.icons{
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 10px;
}

.icon{
    width: 40px;
    height: 40px;
    transition: transform 0.2s ease;
}

.icon:hover{
    transform: scale(1.2);
    cursor: pointer;
}